// footer

.footer {
    width: 100%;
    height: auto;
    background: #2D343C;
    margin-top: 180px;

    font-family: Raleway;
    font-size: 14px;
    font-style: normal;
    font-weight: 400;
    line-height: 16px;
    letter-spacing: 0.05em;
    text-align: left;

    color: #ffffff;
}

.footercontainer {
    width: 1100px;
    margin: 0 auto;
    padding-top: 72px;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: flex-start;
}

.footer_list1,
.footer_list2,
.footer_list3 {
    list-style: none;
}

.footer_link {
    text-decoration: none;
    display: block;
    padding: 5px;
    color: #ffffff;
    cursor: pointer;
}

.footer_link:hover {
    color: #000000;
    background-color: #ffffff;
}

.social {
    display: flex;
    flex-direction: row;
    justify-content: flex-start;
    align-items: center;
}

.gold {
    color: #D4C17F;
}
